import { LampData } from "./system/add-equipment/lamp-data";

export class SampleDeviceData {
  id?: string;
  floor?: string;
  location?: string;
  description?: string;
  designation?: string;
  nameEN?: string;
  nameTC?: string;
  nameSC?: string;
  order?: number;
  systemId?: string;
  lamp?: LampData[];
  groupFilterIds?: string[]; 

  constructor(options?: {
    id?: string
    floor?: string,
    location?: string,
    description?: string,
    designation?: string,
    nameEN?: string,
    nameTC?: string,
    nameSC?: string,
    order?: number
    systemId?: string
    lamp?: LampData[]
    groupFilterIds?: string[]
  }) {
    this.id = options.id ? options.id : new Date().getTime().toString();
    this.floor = options?.floor;
    this.location = options?.location;
    this.description = options?.description;
    this.designation = options?.designation;
    this.nameEN = options?.nameEN;
    this.nameTC = options?.nameTC;
    this.nameSC = options?.nameSC;
    this.order = options?.order;
    this.systemId = options?.systemId;
    this.lamp = options?.lamp;
    this.groupFilterIds = options?.groupFilterIds;
  }
}