//for experimental use only
export class SampleSystemData {
    nameEN?: string;
    nameTC?: string;
    nameSC?: string;
    id?: string;
    order?: number;
    listOfDevs?: any[];

    // getNumberOfSystems() {
    //     return localStorage.getItem("system-list").length;
    // }

    constructor(options? : {id?:string, nameEN: string, nameTC: string, nameSC: string, order?: number}) {
        this.id = options.id ? options.id : new Date().getTime().toString();
        this.nameEN = options.nameEN;
        this.nameTC = options.nameTC;
        this.nameSC = options.nameSC;
        this.order = options.order ? options.order : -1;
        this.listOfDevs = [];
    }
}

//real server data
export class SystemSettingData {
    order?: string;
    systemID?: string;
    systemEN?: string;
    systemCN?: string;
    systemSC?: string;

    constructor(options? : {id?:string, order?: string, systemID?: string, systemEN?: string, systemCN?: string, systemSC?: string}) {
        this.order = options?.order;
        this.systemID = options?.systemID;
        this.systemEN = options?.systemEN;
        this.systemCN = options?.systemCN;
        this.systemSC = options?.systemSC;
    }

    fromJSON(systemData: any) {
        return new SystemSettingData({
            order: systemData.order,
            systemID: systemData.systemID,
            systemEN: systemData.system_EN,
            systemCN: systemData.system_CN,
            systemSC: systemData.system_SC
        });
    }

    // {
    //     "order": "0",
    //     "system_ID": "12",
    //     "system_EN": "Name_EN",
    //     "system_CN": "Name_CN",
    //     "system_SC": "Name_SC"
    // },

}