import { SampleSystemData } from "../sample-system-data";
import { GroupFilter } from "./group-filters/group-filter";

export class SystemUtils {
    static getListFromLocalStorage() {
        return JSON.parse(localStorage.getItem('system-list')) ?? [];
      }

    static getGroupFilterListFromLocalStorage(){
      return JSON.parse(localStorage.getItem('group-filter-list')) ?? [];
    }
    
    static saveListToLocalStorage(systemsArr: SampleSystemData[]) {
        // systemsArr.forEach(system => {
        //     console.log("EN:",system.nameEN,"; TC:",system.nameTC,"; CN:",system.nameSC,"; order:",system.order, "id:", system.id);
        //   });
        localStorage.setItem('system-list', JSON.stringify(systemsArr));
      }

    static saveGroupFilterListToLocalStorage(groupFilterArray: GroupFilter[]) {
      // groupFilterArray.forEach(groupFilter => {
      //   console.log("EN:",groupFilter.nameEN,"; TC:",groupFilter.nameTC,"; CN:",groupFilter.nameSC,"; id:", groupFilter.id);
      // });
      localStorage.setItem('group-filter-list', JSON.stringify(groupFilterArray));
    }

    static reassignOrder(srcSystemList: SampleSystemData[]) {
        let result = [];
        for (let i = 0; i < srcSystemList.length; i++) {
            srcSystemList[i].order = i + 1;
            result.push(srcSystemList[i]);
        }
        return result;
    }

    static updateItemToList(system: SampleSystemData, criteria: string) {
      let systemsArr = SystemUtils.getListFromLocalStorage();
      let targetIndex = -1;
      // criteria can be either 'id', 'nameEN', 'order'
      if (criteria == "id") {
         targetIndex = systemsArr.findIndex((s) => s.id === system.id);
      } else if (criteria == "order") {
         targetIndex = systemsArr.findIndex((s) => s.order == system.order);
      } else if (criteria == "nameEN") {
         targetIndex = systemsArr.findIndex((s) => s.nameEN === system.nameEN);
      }

      if (targetIndex != -1) {
        systemsArr.splice(targetIndex, 1, system)
        SystemUtils.saveListToLocalStorage(systemsArr);
      } else {
        console.error("fail to update item in system-list");
      }
    }
}