import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { Location } from '@angular/common';
import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { SampleDeviceData } from 'src/app/sample-device-data';
import { EquipmentUtils } from '../equipment-utils';
import { AddEquipmentComponent } from '../add-equipment/add-equipment.component';
import { SampleSystemData } from 'src/app/sample-system-data';
import { SystemUtils } from '../system-utils';
import { FormControl, FormGroup } from '@angular/forms';

@Component({
  selector: 'app-list-equipment',
  templateUrl: './list-equipment.component.html',
  styleUrls: ['./list-equipment.component.css'],
})
export class ListEquipmentComponent {
  static route = 'list-equipment';

  showMenu = true;
  siteLanguage = 'English';
  languageList = [
    { code: 'en', label: 'English' },
    { code: 'zh_HK', label: '繁體中文' },
    { code: 'zh_CN', label: '简体中文' },
  ];
  currentLanguage?: String;

  devices = [];
  systemList: SampleSystemData[];

  myFormGroup: FormGroup;

  sortArray(deviceList: SampleDeviceData[]) {
    deviceList.sort((a, b) => {
      if (a.order > b.order) {
        return 1;
      }
      if (a.order < b.order) {
        return -1;
      }
      return 0;
    });

    //reassign the order in the device object
    this.devices = EquipmentUtils.reassignOrder(deviceList);
  }

  setLanguage(translate: TranslateService) {
    let savedLang = localStorage.getItem('LANG');
    if (savedLang != null) {
      this.siteLanguage = savedLang;
      translate.use(savedLang);
      this.currentLanguage = translate.currentLang;
      console.log('currentLanguage', this.currentLanguage);
    }
  }

  getSystemName(systemId: string) {
    let system = this.systemList.find((s) => s.id == systemId);
    if (system) {
      return system.nameEN;
    }
    return '*Unclassfied';
  }

  getEquipmentsFromLocalStorage() {
    //retrieve the list from local storage
    let index = 0;
    let deviceArr = EquipmentUtils.getListFromLocalStorage();

    this.sortArray(deviceArr);

    console.log('length:', this.devices.length);
  }

  ngOnInit() {
    this.systemList = SystemUtils.getListFromLocalStorage();
  }

  constructor(
    private translate: TranslateService,
    private router: Router,
    private location: Location
  ) {
    this.myFormGroup = new FormGroup({
      systemId: new FormControl()
    });

    this.setLanguage(translate);

    setTimeout(() => {
      this.getEquipmentsFromLocalStorage();
    }, 1000);
  }

  drop(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.devices, event.previousIndex, event.currentIndex);
  }

  changeSiteLanguage(localeCode: string): void {
    const selectedLanguage = this.languageList
      .find((language) => language.code === localeCode)
      ?.label.toString();

    if (selectedLanguage) {
      this.siteLanguage = selectedLanguage;
      this.translate.use(localeCode);
      console.log('localCode', localeCode);
      localStorage.setItem('LANG', localeCode);
    }
    setTimeout(() => {
      this.currentLanguage = this.translate.currentLang;
      console.log('currentLanguage', this.currentLanguage);
    }, 300);
  }

  createNew() {
    this.navigateTo(AddEquipmentComponent.route);
  }

  editDevice(device: SampleDeviceData) {
    //TODO: find a better way to pass device data
    console.log('encoded :', JSON.parse(JSON.stringify(device)));
    this.navigateTo(AddEquipmentComponent.route, {
      edit: true,
      device: JSON.stringify(device),
    });
  }

  navigateTo(path: string, customParams?: any) {
    // this.router.navigateByUrl(path);
    this.router.navigate([path], { queryParams: customParams });
  }

  backToLastPage() {
    this.location.back();
  }
}
