import { Component, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { Location } from '@angular/common';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { AddSystemSubmitDialogComponent } from './add-system-submit-dialog/add-system-submit-dialog.component';
import { SampleSystemData } from 'src/app/sample-system-data';
import { ConfirmDeleteSystemDialogComponent } from '../confirm-delete-system-dialog/confirm-delete-system-dialog.component';
import { SystemUtils } from '../system-utils';
import { ConvertUtils } from 'src/app/convert-utils';
import { FormGroup, FormControl } from '@angular/forms';
import { RouterExtService } from 'src/app/service/route.service';
import { SharedService } from 'src/app/service/shared.service';
import { Message, MessageUtils } from 'src/app/message';
import { UserService } from 'src/app/service/user.service';
import { WebsocketService } from 'src/app/service/websocket.service';
import { environment } from 'src/environments/environment';
import { ProjectDetail } from 'src/app/project/project';

@Component({
  selector: 'app-add-system',
  templateUrl: './add-system.component.html',
  styleUrls: ['./add-system.component.css'],
})
export class AddSystemComponent implements OnDestroy {
  static route = 'system-setup';
  edit = false;
  system?: SampleSystemData;
  previousUrl = undefined;
  showMenu = true;
  siteLanguage = 'English';
  languageList = [
    { code: 'en', label: 'English' },
    { code: 'zh_HK', label: '繁體中文' },
    { code: 'zh_CN', label: '简体中文' },
  ];
  currentLanguage?: String;

  //environment configuration (refer to /environment)
  isDev: boolean = false;

  lampSelection = new FormControl('');
  lampSelectionList: string[] = [
    'Lamp ON',
    'Lamp Off',
    'Normal',
    'Fault',
    'Unknown',
    'Power Failure',
  ];

  groupSelection = new FormControl('');
  groupSelectionList: string[] = [
    'Critical System',
    'Operation',
    'Spare Kit',
    'Special',
  ];

  private dialogRef?: MatDialogRef<AddSystemSubmitDialogComponent>;
  private confirmDialogRef?: MatDialogRef<ConfirmDeleteSystemDialogComponent>;

  myForm: FormGroup;
  project: ProjectDetail;

  updateTime: string;

  convertToSimplifiedChinese() {
    const _nameTC = this.myForm.get('nameTC')?.value;
    this.myForm
      .get('nameSC')
      ?.setValue(ConvertUtils.convertToSimplifiedChinese(_nameTC));
  }

  constructor(
    private translate: TranslateService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private location: Location,
    private dialog: MatDialog,
    private routerExtService: RouterExtService,
    private sharedService: SharedService,
    private userService: UserService,
    private websocketService: WebsocketService
  ) {
    let savedLang = localStorage.getItem('LANG');
    if (savedLang != null) {
      this.siteLanguage = savedLang;
      translate.use(savedLang);
      this.currentLanguage = translate.currentLang;
      console.log('currentLanguage', this.currentLanguage);
    }

    console.log('production? : ' + environment.production);
    this.isDev = !environment.production;

    websocketService.reconnect();
    console.log('++++++++ reconnect websocket +++++++++');
    websocketService.messages.subscribe((msg) => {
      console.log('[add-system] Response from websocket: ' + msg);

      if (MessageUtils.isJsonString(msg.toString())) {
        var jObject = JSON.parse(msg.toString());
        if (jObject.type == 'system_DetailEditing_Result') {
          if (jObject.content['result'] == 'Success') {
            if (this.system == null) {
              this.openSubmittedDialog();
            } else {
              this.location.back();
            }
          } else {
            //open fail dialog
          }
        }
      }
    });

    //set isEditSystem to true
    this.sharedService.isEditSystem = true;

    //get previous navigation
    this.routerExtService.getPreviousUrl().then((value) => {
      this.previousUrl = value;
      console.log('previousUrl', this.previousUrl);
    });

    //form validation
    this.myForm = new FormGroup({
      nameEN: new FormControl(''),
      nameTC: new FormControl(''),
      nameSC: new FormControl(''),
    });

    //get session storage here
    const formJsonString = sessionStorage.getItem('systemFormDetail');
    console.log('systemFormDetail : ', formJsonString);
    if (formJsonString != null) {
      const formDetail = JSON.parse(formJsonString);
      this.myForm.get('nameEN')?.setValue(formDetail.nameEN);
      this.myForm.get('nameTC')?.setValue(formDetail.nameTC);
      this.myForm.get('nameSC')?.setValue(formDetail.nameSC);
    }

    //To use "edit", use {queryParams : {'edit':true}} in navigator
    this.edit = this.activatedRoute.snapshot.queryParams['edit'] ?? false;
    console.log('edit :', this.edit);
    if (this.edit) {
      this.system = JSON.parse(
        this.activatedRoute.snapshot.queryParams['system']
      );
      //prefill the data
      this.myForm.get('nameEN')?.setValue(this.system?.nameEN);
      this.myForm.get('nameTC')?.setValue(this.system?.nameTC);
      this.myForm.get('nameSC')?.setValue(this.system?.nameSC);
    }

    //get update time
    this.getUpdateTime();
  }
  ngOnDestroy(): void {
    this.sharedService.isEditSystem = false;
    sessionStorage.removeItem('systemFormDetail');
  }

  changeSiteLanguage(localeCode: string): void {
    const selectedLanguage = this.languageList
      .find((language) => language.code === localeCode)
      ?.label.toString();

    if (selectedLanguage) {
      this.siteLanguage = selectedLanguage;
      this.translate.use(localeCode);
      console.log('localCode', localeCode);
      localStorage.setItem('LANG', localeCode);
    }
    setTimeout(() => {
      this.currentLanguage = this.translate.currentLang;
      console.log('currentLanguage', this.currentLanguage);
    }, 300);
  }

  reset() {
    this.myForm.reset();
    sessionStorage.removeItem("systemFormDetail");
  }

  openSubmittedDialog() {
    this.dialogRef = this.dialog.open(AddSystemSubmitDialogComponent, {
      width: '450px',
    });

    this.dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
      if (result != null) {
        if (result['reset'] == true) {
          this.myForm.reset();
        } else if (result['path'] != null) {
          if (this.previousUrl == '/system-list') {
            // go back
            this.location.back();
          } else {
            // Navigating to a new page ('/system-list') and replacing the current page ('/system-setup')
            this.router.navigateByUrl(result['path'], { replaceUrl: true });
          }
        }
      }
    });
  }

  getProjectId() {
    return localStorage.getItem('projectId');
  }

  _sendUpdateSystemResponse() {
    let message: Message = {
      type: '',
      content: {},
    };

    // message.type = Constants.UPDATE_SYSTEM_SETTING_RESULT;
    // message.content = {
    //   "result" : "Success",
    //   "detail" : "Updating the system list",
    //   "processid" : "process-id-1",
    //   "systems": [
    //     {
    //       "order": 1,
    //       "system_ID": "system-id-1",
    //     },
    //     {
    //       "order": 2,
    //       "system_ID": "system-id-2"
    //     }
    //   ]
    // }

    message.type = 'system_DetailEditing_Result';
    message.content = {
      username: 'usr',
      result: 'Success',
      order: 1,
      system_ID: 'system-id-1',
    };

    this.websocketService.messages.next(message);
  }

  //get unixtime now
  getUpdateTime() {
    //Get Time API
    const request: RequestInfo = new Request(
      'https://worldtimeapi.org/api/timezone/Asia/Hong_Kong',
      {
        method: 'GET',
      }
    );

    return (
      fetch(request)
        // the JSON body is taken from the response
        .then((res) => {
          let promise = res.json();
          promise.then((value) => {
            console.log('update time : ', value.unixtime);
            this.updateTime = value.unixtime;
          });
        })
        .catch((err) => {
          console.log('get time erorr: ', err);
          this.updateTime = (Date.now() / 1000).toFixed(0);
        })
    );
  }

  submitForm() {
    console.log(this.myForm.get('nameEN')?.value);
    console.log(this.myForm.get('nameTC')?.value);
    console.log(this.myForm.get('nameSC')?.value);

    const formDetail = {
      nameEN: this.myForm.get('nameEN')?.value,
      nameTC: this.myForm.get('nameTC')?.value,
      nameSC: this.myForm.get('nameSC')?.value,
    };

    sessionStorage.setItem('systemFormDetail', JSON.stringify(formDetail));

    //custom validation
    if (
      this.myForm.get('nameEN')?.value == '' ||
      this.myForm.get('nameEN')?.value == null
    ) {
      this.myForm.get('nameEN')?.setErrors({ required: true });
    }
    if (
      this.myForm.get('nameTC')?.value == '' ||
      this.myForm.get('nameTC')?.value == null
    ) {
      this.myForm.get('nameTC')?.setErrors({ required: true });
    }
    if (
      this.myForm.get('nameSC')?.value == '' ||
      this.myForm.get('nameSC')?.value == null
    ) {
      this.myForm.get('nameSC')?.setErrors({ required: true });
    }

    if (this.myForm.invalid) {
      //error thrown if fields are empty
      return;
    }

    let systemsArr = SystemUtils.getListFromLocalStorage();
    if (this.system == null) {
      //Add new system
      let newOrder = SystemUtils.getListFromLocalStorage().length + 1 ?? 1;
      let newSystem = new SampleSystemData({
        nameEN: this.myForm.get('nameEN')?.value,
        nameTC: this.myForm.get('nameTC')?.value,
        nameSC: this.myForm.get('nameSC')?.value,
        order: newOrder,
      });
      console.log('new system id:', newSystem.id);
      systemsArr.push(newSystem);
    } else {
      //Edit existing system
      let currentSystemIndex = systemsArr.findIndex(
        (s) => s.id === this.system.id
      );
      systemsArr[currentSystemIndex] = new SampleSystemData({
        id: this.system.id,
        nameEN: this.myForm.get('nameEN')?.value,
        nameTC: this.myForm.get('nameTC')?.value,
        nameSC: this.myForm.get('nameSC')?.value,
        order: currentSystemIndex + 1,
      });
    }

    //get project id
    let projectId = this.getProjectId();
    //get all systems
    let systemList = SystemUtils.getListFromLocalStorage();
    //get device data for each system

    let message: Message = {
      type: '',
      content: {},
    };

    // message.type = Constants.UPDATE_SYSTEM_SETTING;
    // message.content = {
    //   "username" : this.userService.getUsername(),
    //   "project_ID" : projectId,
    //   "update_time" : updateTime,
    //   "systems": systemsArr,
    // }

    message.type = 'system_DetailEditing';
    if (this.system == null) {
      //Submit new system through websocket
      message.content = {
        action: 'create',
        username: this.userService.getUsername(),
        project_ID: projectId,
        update_time: this.updateTime,
        system: {
          system_EN: this.myForm.get('nameEN')?.value,
          system_CN: this.myForm.get('nameTC')?.value,
          system_SC: this.myForm.get('nameSC')?.value,
        },
      };
    } else {
      //Submit edit system through websocket
      message.content = {
        action: 'edit',
        system_ID: this.system.id,
        username: this.userService.getUsername(),
        project_ID: projectId,
        update_time: this.updateTime,
        system: {
          system_EN: this.myForm.get('nameEN')?.value,
          system_CN: this.myForm.get('nameTC')?.value,
          system_SC: this.myForm.get('nameSC')?.value,
        },
      };
    }

    this.websocketService.messages.next(message);
    let isDev = !environment.production;

    if (isDev) {
      this._sendUpdateSystemResponse();
    }

    SystemUtils.saveListToLocalStorage(systemsArr);
  }

  removeSystem(options?: { id?: string; projectID?: string }) {
    let message: Message = {
      type: '',
      content: {},
    };

    message.type = 'system_DetailEditing';
    message.content = {
      action: 'remove',
      equipment_ID: options.id,
      username: this.userService.getUsername(),
      projectID: options.projectID,
      update_time: this.updateTime,
    };

    this.websocketService.messages.next(message);

    if (this.isDev) {
      this._sendUpdateSystemResponse();
    }
  }

  remove(system: SampleSystemData) {
    this.confirmDialogRef = this.dialog.open(
      ConfirmDeleteSystemDialogComponent,
      {
        width: '450px',
        data: 'system',
      }
    );

    this.confirmDialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);

      //If User Click Yes to Delete
      if (result != null && result['confirm'] == true) {
        let systemsArr = SystemUtils.getListFromLocalStorage();
        const index = systemsArr.findIndex((sys) => sys.id === system.id);
        systemsArr.splice(index, 1);

        SystemUtils.saveListToLocalStorage(systemsArr);

        //get project id
        let projectId = this.getProjectId();

        //remove existing equipment through websocket
        this.removeSystem({
          id: system.id,
          projectID: projectId,
        });
      }
    });
  }

  navigateTo(path: string) {
    this.router.navigateByUrl(path);
  }

  backToLastPage() {
    this.location.back();
  }
}
