import { Equipment, EquipmentSetting, SystemDetail } from "../equipment-setting";
import { SampleDeviceData } from "../sample-device-data";
import { LampData } from "./add-equipment/lamp-data";

export class EquipmentUtils {
    static getListFromLocalStorage() {
        return JSON.parse(localStorage.getItem('device-list')) ?? [];
      }
    
    static saveListToLocalStorage(equipmentsArr: SampleDeviceData[]) {
        // equipmentsArr.forEach(equipment => {
        //     console.log("EN:",equipment.nameEN,"; TC:",equipment.nameTC,"; CN:",equipment.nameSC,"; order:",equipment.order);
        //   });
        localStorage.setItem('device-list', JSON.stringify(equipmentsArr));
      }

    //1. get equipment setting
    static getEquipmentSettingToLocalStorage() : EquipmentSetting {
      let setting = new EquipmentSetting();
      let parse = JSON.parse(localStorage.getItem('equipment-setting'));
      return Object.assign(setting, parse);
    }

    //2. save equipment setting
    static saveEquipmentSettingToLocalStorage(setting: EquipmentSetting) {
      localStorage.setItem('equipment-setting', JSON.stringify(setting));
    }

    //3. get equipment list *make sure you have saved equipment-setting
    static getEquipmentBySystemId(systemId: string) {
      let settingStr = localStorage.getItem('equipment-setting');
      if (settingStr != null) {
        let setting = JSON.parse(settingStr) as EquipmentSetting;
        let systemDetail = setting.detail.filter(e => e.systemID === systemId) as SystemDetail;
        let equipmentList = systemDetail.equipments as Equipment[] ?? [];
        return equipmentList;
      }
      return [];
    }

    //4. save equipment to equipment setting
    static saveEquipmentToSetting(equipment: Equipment, systemId: string) {
      let settingStr = localStorage.getItem('equipment-setting');
      let setting = JSON.parse(settingStr) as EquipmentSetting;
      let systemDetail = setting.detail.filter(e => e.systemID === systemId) as SystemDetail;
      if (systemDetail.equipments == null) {
        systemDetail.equipments = [];
      }
      let equipments = systemDetail.equipments;
      equipments.push(equipment);

      localStorage.setItem('equipment-setting', JSON.stringify(setting));
    }

    static saveLampDataListToLocalStorage(lampDataList) {
        localStorage.setItem('lamp-data', JSON.stringify(lampDataList));
    }

    static getLampDataListFromLocalStorage() : LampData[] {
      let retString = localStorage.getItem('lamp-data');
      let r = JSON.parse(retString); //json array
      let lampDataList = [];

      r.forEach((lamp) => {
        let lampData = LampData.fromJSON(lamp);

        lampDataList.push(lampData);

        console.log("nameEN : ",lampData.nameEN)
      });

      return lampDataList;
    }

    static reassignOrder(srcList: SampleDeviceData[]) {
        let result = [];
        for (let i = 0; i < srcList.length; i++) {
          srcList[i].order = i + 1;
            result.push(srcList[i]);
        }
        return result;
    }
}